<template>
  <v-container>
    <v-layout
      text-xs-center
      wrap
    >
      <v-flex xs12>
        <v-img
          :src="require('../assets/img/logo.svg')"
          class="my-3"
          contain
          height="200"
        ></v-img>
      </v-flex>

      <v-flex xs12>
        <h1 class="display-2 font-weight-bold mb-3">
          {{ $t("message.hello") }} {{ username }}
        </h1>
        <h2>width: {{ width }}</h2>
        <h2>height: {{ height }}</h2>
        <p class="subheading font-weight-regular">
          <br>
          <router-link to="/checkIn">Check-in</router-link>
          <br>
          <router-link to="/preCheckIn">Pre Check-in</router-link>
          <br>
          <v-btn @click.native="signOut">
            signOut
          </v-btn>
        </p>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AmplifyStore from '@/store'
import { AmplifyEventBus } from 'aws-amplify-vue'
import * as AmplifyModules from 'aws-amplify'


export default {
  data() {
    return {

      // height: window.screen.height,
      // width: window.screen.width

      // width: document.body.clientWidth,
      // height: document.body.clientHeight
    }
  },
  mounted () {
    
    console.log(JSON.stringify(this.$store.state.amplify.user.username));

  },
  computed: {
    username: {
      get() {
        return this.$store.state.amplify.user.username
      }
    },
    width: {
      get() {
        // return document.documentElement.clientWidth
        return this.$window.width
      }
    },
    height: {
      get() {
        // return document.documentElement.clientHeight
        return this.$window.height
      }
    }
  },
  methods: {

    async signOut () {
      await AmplifyModules.Auth.signOut()
      AmplifyEventBus.$emit('authState', 'signedOut')
    }
  },
}
</script>

